import Vue from "vue";
import VueRouter from "vue-router";
import LoginPage from "@/views/LoginPage.vue";
import Home from "@/views/Home"

Vue.use(VueRouter);

const routes = [
  {
    path: "/owner/login",
    name: "LoginPage",
    component: LoginPage,
  },
  {
    path: "/owner",
    name: "Home",
    component: Home,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
